// 导入数据请求方法
import { getSpecsList,getSpecsTotal } from '../../request/specs'
export default {
    namespaced:true,
    state(){
        return {
            // 规格列表
            specsList:[],
            // 规格数据的总条数
            specsTotal:0
        }
    },
    mutations:{
        specsListInit(state,list){
            state.specsList=list
        },
        specsTotalInit(state,total){
            state.specsTotal=total
        }
    },
    actions:{
        // 请求规格列表
        // commit: 系统自动注入, 提交一个mutation方法
        // params={page,size},组件来传递
        getSpecsListAction({commit},params){
            // 数据请求方法
            getSpecsList(params).then(list=>{
                // 通过commit触发specsListInit,实现数据更新
                commit('specsListInit',list)
            })
        },
        getSpecsTotalAction({commit}){
            // 数据请求方法
            getSpecsTotal().then(total=>{
                // 通过commit触发specsTotalInit,实现数据更新
                commit('specsTotalInit',total)
            })
        },
    }
}


