import Vue from 'vue'
import Router from 'vue-router'

import {
  Message
} from 'element-ui'

// 导入页面组件
const Login = () => import('../pages/Login')
const Layout = () => import('../pages/Layout')

// 菜单管理
const Menu = () => import('../pages/menu/Index')
// 角色管理
const Role = () => import('../pages/role/Index')
// 管理员管理
const Admin = () => import('../pages/admin/Index')
// 分类管理
const Category = () => import('../pages/category/Index')
// 规格管理
const Specs = () => import('../pages/specs/Index')
// 商品管理
const Goods = () => import('../pages/goods/Index')

// 会员管理
const Member = () => import('../pages/member/Index')

// 轮播图管理
const Banner=()=>import('../pages/banner/Index')

// 秒杀管理
const Seckill=()=>import('../pages/seckill/Index')

// 数据可视化组件
const Home = () => import('../pages/Home')
// 关于stylus语法的测试
const StylusPage = () => import('../pages/StylusPage')


Vue.use(Router)

// 路由规则数据
const routes = [{
    path: '/',
    name: 'Layout',
    component: Layout,
    redirect: '/home',
    meta: {
      title: '布局组件'
    },
    children: [{
        path: '/home',
        component: Home,
        meta: {
          title: '数据可视化'
        }
      },
      {
        path: '/menu',
        component: Menu,
        meta: {
          title: '菜单管理'
        }
      },
      {
        path: '/role',
        component: Role,
        meta: {
          title: '角色管理'
        }
      },
      {
        path: '/admin',
        component: Admin,
        meta: {
          title: '管理员管理'
        }
      },
      {
        path: '/category',
        component: Category,
        meta: {
          title: '分类管理'
        }
      },
      {
        path: '/specs',
        component: Specs,
        meta: {
          title: '规格管理'
        }
      },
      {
        path: '/goods',
        component: Goods,
        meta: {
          title: '商品管理'
        }
      },
      {
        path:'/member',
        component:Member,
        meta: {
          title: '会员管理'
        }
      },
      {
        path:'/banner',
        component:Banner,
        meta: {
          title: '轮播图管理'
        }
      },
      {
        path:'/seckill',
        component:Seckill,
        meta: {
          title: '秒杀管理'
        }
      },
      {
        path: '/stylus',
        component: StylusPage
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: '登录页面'
    }
  }
]

// 创建路由实例
const router = new Router({
  routes
})

// 路由导航守卫
router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    // 判断登录状态
    const loginInfo = JSON.parse(localStorage.getItem('logininfo') || '{}')
    if (loginInfo.token === '' || loginInfo.token === undefined) {
      Message({
        type: 'error',
        message: '请登录'
      })
      return next('/login')
    }
  }
  // 设置页面标题
  document.title=to.meta.title+'-小U商城后台管理系统'
  next()
})

export default router



