// 导入数据请求方法
import { getRoleList } from '../../request/role'
export default {
    // 开启独立命名空间
    namespaced:true,
    state(){
        return {
            // 角色列表
            roleList:[]
        }
    },
    mutations:{
        // 初始化roleList的方法
        roleListInit(state,list){
            state.roleList=list
        }
    },
    actions:{
        getRoleListAction({commit}){
            getRoleList().then(list=>{
                if(list){
                    commit('roleListInit',list)
                }
            })
        }
    }
}


