// 导入数据请求方法
import { getAdminList,getAdminTotal } from '../../request/admin'
export default {
    namespaced:true,
    state(){
        return {
            // 管理列表
            adminList:[],
            // 数据总条数
            adminTotal:0
        }
    },
    mutations:{
        // 初始化adminlsit
        adminListInit(state,list){
            state.adminList=list
        },
        adminTotalInit(state,list){
            state.adminTotal=list
        },
        // 删除
        adminDel(state,uid){
            // 根据id查找索引
            const index=state.adminList.findIndex(item=>item.uid===uid)
            // 直接根据索引删除数组元素
            state.adminList.splice(index,1)
        }
    },
    actions:{
        // 获取管理员列表 parmas={page,size}
        getAdminListAction({commit},parmas){
            getAdminList(parmas).then(list=>{
                if(list){
                    commit('adminListInit',list)
                }
            })
        },
        getAdminTotal({commit}){
            getAdminTotal().then(total=>{
                if(total){
                    commit('adminTotalInit',total)
                }
            })
        }      
    }
}


