// 导入数据请求方法
import { getMenuList } from '../../request/menu'
export default {
    // 开启独立命名空间
    namespaced:true,
    state(){
        return {
            // 菜单列表
            menuList:[]
        } 
    },
    mutations:{
        // 初始化菜单列表
        menuListInit(state,list){
            state.menuList=list
        }
    },
    actions:{
        getMenuListAction(context){
            // 发请求, 拿数据, commit一个mutation方法, 实现数据同步 
            getMenuList().then(list=>{
                if(list){   // 成功
                    context.commit('menuListInit',list)
                }
            })
        }
    }
}


