// 导入数据请求方法
import { getCateList } from '../../request/category'
export default {
    namespaced:true,
    state(){
        return {
            // 分类列表
            cateList:[]
        }
    },
    mutations:{
        cateListInit(state,list){
            state.cateList=list
        }
    },
    actions:{
        getCateListActoin({commit}){
            // 发请求, 获取数据, 触发同步mutation方法cateListInit更新数据cateList
            getCateList().then(list=>{
                if(list){
                    // 通过commit提交cateListInit
                    commit('cateListInit',list)
                }
            })
        }
    }
}


