// 专门发数据请求: 菜单相关的数据请求
// 导入axios
import axios from '../utils/http'

// 请求菜单列表
export async function getMenuList(){
    const res=await axios.get('/menulist?istree=1')
    if(res.code===200){
        return res.list
    }   
    return false
}


